import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

const DEFAULT_TITLE: string = 'Â¿EstÃ¡ seguro que debe anular esta informaciÃ³n?';
const DEFAULT_CONTENT: string = 'Al anular este concepto, quedarÃ¡ la huella de todo lo que se hizo apoyados en su informaciÃ³n. No es una eliminaciÃ³n tÃ¡cita, es suprimir su uso en adelante.';

@Component({
  selector: 'hel-alert-delete-data-helisa',
  templateUrl: './alert-delete-data-helisa.component.html',
  styleUrls: ['./alert-delete-data-helisa.component.sass']
})
export class AlertDeleteDataHelisaComponent implements OnInit {

  title: string;
  content: string;
  okLabel: string;
  cancelLabel: string;
z
  constructor(
    public dialogRef: MatDialogRef<AlertDeleteDataHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AlertDeleteDataHelisaComponent
  ) { 
    this.title = data.title;
    if (this.title === undefined) {
      this.title = DEFAULT_TITLE;
    }
    this.content = data.content;
    if (this.content === undefined) {
      this.content = DEFAULT_CONTENT;
    }
    this.okLabel = data.okLabel;
    if (this.okLabel === undefined) {
      this.okLabel = 'Lo asumo';
    }
    this.cancelLabel = data.cancelLabel;
    if (this.cancelLabel === undefined) {
      this.cancelLabel = 'Me retracto';
    }
    dialogRef.disableClose = true;
    dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
      if (event.code === 'Escape') {
        this.dialogRef.close(this.onCancel());
      }
    });    
  }

  ngOnInit() {
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
