import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { AlertLostDataHelisaComponent } from './alert-lost-data-helisa.component';



@Injectable({
  providedIn: 'root'
})
export class AlertLostDataHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(title?: string, content?: string, okLabel?: string, cancelLabel?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertLostDataHelisaComponent> = this.dialog.open(AlertLostDataHelisaComponent, {
      width: '250px',
      data: { title, content, okLabel, cancelLabel }
    });

    return dialogRef.afterClosed();
  }
}
