import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


interface AlertInformationNotValidProperties {
    title: string;
    content: string;
}

const DEFAULT_CONTENT: string = 'Rectifique. Hay informaciÃ³n no vÃ¡lida';

@Component({
    selector: 'hel-alert-information-not-valid-helisa',
    templateUrl: './alert-information-not-valid-helisa.component.html',
    styleUrls: ['./alert-information-not-valid-helisa.component.sass']
})
export class AlertInformationNotValidHelisaComponent implements OnInit {

    content: string;

    constructor(
        public dialogRef: MatDialogRef<AlertInformationNotValidHelisaComponent>,
        @Inject(MAT_DIALOG_DATA) public data: AlertInformationNotValidProperties
    ) {
        this.content = data.content;
        if (this.content === undefined) {
            this.content = DEFAULT_CONTENT;
        }
        dialogRef.disableClose = true;
        dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
            if (event.code === 'Escape') {
                this.dialogRef.close(this.onCancel());
            }
        });
    }

    ngOnInit(): void {
        setTimeout(() => {
            this.dialogRef.close(this.onCancel());
        }, 3000);
    }

    onCancel(): void {
        this.dialogRef.close();
    }
}
