import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import {AlertUncompletedSelectedDataHelisaComponent} from './alert-uncompleted-selected-data-helisa.component';



@Injectable({
  providedIn: 'root'
})
export class AlertUncompletedSelectedDataHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(title?: string, content?: string, okLabel?: string, cancelLabel?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertUncompletedSelectedDataHelisaComponent> = this.dialog.open(AlertUncompletedSelectedDataHelisaComponent, {
      width: '250px',
      data: { title, content, okLabel, cancelLabel }
    });

    return dialogRef.afterClosed();
  }
}
