import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

interface AlertUncompletedDataHelisaProperties {
  title: string;
  content: string;
  okLabel: string;
  cancelLabel: string;
}

const DEFAULT_TITLE: string = 'No ha suministrado la informaciÃ³n necesaria.';
const DEFAULT_CONTENT: string = 'Si insite en grabar asÃ­, este concepto no serÃ¡ utilizable hasta su conclusiÃ³n satisfactoria, que deberÃ¡ completar posteriormente modificando en concepto.';

@Component({
  selector: 'hel-alert-uncompleted-data-helisa',
  templateUrl: './alert-uncompleted-data-helisa.component.html',
  styleUrls: ['./alert-uncompleted-data-helisa.component.sass']
})
export class AlertUncompletedDataHelisaComponent implements OnInit {

  title: string;
  content: string;
  okLabel: string;
  cancelLabel: string;


  constructor(
    public dialogRef: MatDialogRef<AlertUncompletedDataHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AlertUncompletedDataHelisaProperties
  ) { 
    this.title = data.title;
    if (this.title === undefined) {
      this.title = DEFAULT_TITLE;
    }
    this.content = data.content;
    if (this.content === undefined) {
      this.content = DEFAULT_CONTENT;
    }
    this.okLabel = data.okLabel;
    if (this.okLabel === undefined) {
      this.okLabel = 'Lo asumo';
    }
    this.cancelLabel = data.cancelLabel;
    if (this.cancelLabel === undefined) {
      this.cancelLabel = 'Me retracto';
    }
    dialogRef.disableClose = true;
    dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
      if (event.code === 'Escape') {
        this.dialogRef.close(this.onCancel());
      }
    });    
  }

  ngOnInit() {
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
