import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { AlertInformationNotValidHelisaComponent } from './alert-information-not-valid-helisa.component';



@Injectable({
  providedIn: 'root'
})
export class AlertInformationNotValidHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(title?: string, content?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertInformationNotValidHelisaComponent> = this.dialog.open(AlertInformationNotValidHelisaComponent, {
      width: '250px',
      data: { title, content}
    });

    return dialogRef.afterClosed();
  }
}
