import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

interface AlertUncompletedSelectedDataHelisaProperties {
  title: string;
  content: string;
  okLabel: string;
  cancelLabel: string;
}

const DEFAULT_CONTENT: string = 'Este registro no esta completo. ModifÃ­quelo para poderlo utilizar.';

@Component({
  selector: 'hel-alert-uncompleted-selected-data-helisa',
  templateUrl: './alert-uncompleted-selected-data-helisa.component.html',
  styleUrls: ['./alert-uncompleted-selected-data-helisa.component.sass']
})
export class AlertUncompletedSelectedDataHelisaComponent implements OnInit {

  content: string;
  okLabel: string;

  constructor(
    public dialogRef: MatDialogRef<AlertUncompletedSelectedDataHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AlertUncompletedSelectedDataHelisaProperties
  ) { 
    this.content = data.content;
    if (this.content === undefined) {
      this.content = DEFAULT_CONTENT;
    }
    this.okLabel = data.okLabel;
    if (this.okLabel === undefined) {
      this.okLabel = 'Aceptar';
    }
    dialogRef.disableClose = true;
    dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
      if (event.code === 'Escape') {
        this.dialogRef.close(this.onCancel());
      }
    });    
  }

  ngOnInit(): void {
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
