import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

interface AlertLostDataHelisaProperties {
  title: string;
  content: string;
  okLabel: string;
  cancelLabel: string;
}

const DEFAULT_TITLE: string = 'Â¿EstÃ¡ seguro de querer perder lo ya hecho?';
const DEFAULT_CONTENT: string = 'Si no estÃ¡ seguro, puede continuar o "aplicar" y posteriormente cuando tenga clara las respuestas, usando la opciÃ³n de modificar podrÃ¡ completar el concepto.';

@Component({
  selector: 'hel-alert-lost-data-helisa',
  templateUrl: './alert-lost-data-helisa.component.html',
  styleUrls: ['./alert-lost-data-helisa.component.sass']
})
export class AlertLostDataHelisaComponent implements OnInit {

  title: string;
  content: string;
  okLabel: string;
  cancelLabel: string;

  constructor(
    public dialogRef: MatDialogRef<AlertLostDataHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AlertLostDataHelisaProperties
  ) { 
    this.title = data.title;
    if (this.title === undefined) {
      this.title = DEFAULT_TITLE;
    }
    this.content = data.content;
    if (this.content === undefined) {
      this.content = DEFAULT_CONTENT;
    }
    this.okLabel = data.okLabel;
    if (this.okLabel === undefined) {
      this.okLabel = 'Lo asumo';
    }
    this.cancelLabel = data.cancelLabel;
    if (this.cancelLabel === undefined) {
      this.cancelLabel = 'Me retracto';
    }
    dialogRef.disableClose = true;
    dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
      if (event.code === 'Escape') {
        this.dialogRef.close(this.onCancel());
      }
    });    
  }

  ngOnInit() {
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
